import { DEFAULT_DECK_PART_ARR, } from "yugioh-deck-tool-core/src/main";
import { computed, defineComponent } from "@vue/composition-api";
import YgoPrice from "../YgoPrice.vue";
import { applicationContainer } from "../../inversify.config";
import { APPLICATION_TYPES } from "../../types";
import YgoDeckPart from "./YgoDeckPart.vue";
import { DECK_CARD_REMOVE } from "../../store/modules/deck";
const deckService = applicationContainer.get(APPLICATION_TYPES.DeckService);
export default defineComponent({
    components: {
        YgoDeckPart,
        YgoPrice,
    },
    props: {},
    setup(props, context) {
        const deckParts = DEFAULT_DECK_PART_ARR;
        const allCards = computed(() => deckService.getAllCards(context.root.$store.state.deck.active));
        const onCardRightClicked = (e, deckPart) => {
            context.root.$store.commit(DECK_CARD_REMOVE, {
                card: e.card,
                deckPart: deckPart,
            });
        };
        return { deckParts, allCards, onCardRightClicked };
    },
});
