import { CardTypeGroup } from "yugioh-deck-tool-core/src/main";
import { defineComponent } from "@vue/composition-api";
import YgoCard from "@/components/YgoCard.vue";
export default defineComponent({
    props: {
        cards: {
            required: true,
            type: Array,
        },
    },
    components: {
        YgoCard,
    },
    setup() {
        const createTypeText = (card) => card.type.group === CardTypeGroup.MONSTER
            ? card.type.name
            : card.type.group;
        const createSubTypeText = (card) => card.type.group === CardTypeGroup.MONSTER
            ? `${card.attribute}/${card.subType}`
            : card.subType;
        return { createTypeText, createSubTypeText };
    },
});
